<template>
    <div class="text2image">
        <!-- 当 products 为空时显示占位 div -->
        <div v-if="products.length === 0" class="placeholder"></div>

        <!-- 当 products 不为空时显示 ProductList -->
        <ProductList v-else :products="products" />
        
        <el-form ref="form" :model="form">
            <el-form-item>
                <el-input type="textarea" v-model="form.query" class="custom-textarea" placeholder="请输入查询内容"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import axios from 'axios';
import ProductList from './ProductList.vue';
import request from '../api/request.js'; // 引入公共配置文件

export default {
    name: 'Text2Image',
    components: {
        ProductList
    },
    data() {
        return {
            form: {
                query: ''
            },
            products: []
        };
    },
    methods: {
        async onSubmit() {
            try {
                // 构造 FormData 数据
                const formData = new FormData();
                formData.append('qid', this.form.query); // 添加 qid 字段

                console.log('查询请求数据:', formData);

                // 发送 POST 请求
                const response = await axios.post(`${request.API_BASE_URL}/ret/search_data_all/`, formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });

                // 假设后端返回的数据结构中有一个字段叫 data.products
                console.log(response.data.data.results);
                this.products = response.data.data.results || [];
                console.log('查询成功:', this.products);
            } catch (error) {
                console.error('查询失败:', error);
            }
        }
    }
};
</script>

<style scoped>
.text2image .custom-textarea {
    height: 60px;
    width: 672px;
    font-size: 20px;
}
.placeholder {
    height: 400px; /* 占位高度，可以根据需要调整 */
}
</style>