<template>
  <div id="app">
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark">
          <!-- 监听 SearchNavigate 的 tab-change 事件 -->
          <SearchNavigate @tab-change="handleTabChange" />
        </div>
      </el-col>
      <el-col :span="24" v-if="currentTab === 'first'">
        <div class="grid-content bg-purple-dark">
          <!-- 当 currentTab 为 'first' 时显示 Text2Image -->
          <Text2Image />
        </div>
      </el-col>
      <el-col :span="24" v-if="currentTab === 'second'">
        <div class="grid-content bg-purple-dark">
          <!-- 当 currentTab 为 'first' 时显示 Text2Image -->
          <Image2Text />
        </div>
      </el-col>
    </el-row> 
  </div>
</template>

<script>
import Text2Image from './components/Text2Image.vue';
import SearchNavigate from './components/SearchNavigate.vue';
import Image2Text from './components/Image2Text.vue';

export default {
  name: 'App',
  components: {
    SearchNavigate,
    Text2Image,
    Image2Text,
  },
  data() {
    return {
      currentTab: 'first', // 默认显示 "文本检索图像"
    };
  },
  methods: {
    handleTabChange(tabName) {
      // 更新当前选中的标签名
      this.currentTab = tabName;
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>